* {
  margin: 0;
  padding: 0;
  font-size: 62.5%;
  box-sizing: border-box;
}

.container {
  width: 100%;
  max-width: 1352px;
  margin: 0 auto;
}

.header {
  width: 100%;
  height: 75px;
  box-shadow: 0 3px 6px #00000029;
  background-color: #FFFFFF;
}
.header .content__wrapper {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .logo {
  font-family: "Raleway", sans-serif;
  font-size: 3.2rem;
  font-weight: 300;
  color: #08A6E4;
  text-decoration: none;
  margin-left: 10px;
}
.header .navigation ul li {
  display: none;
  text-decoration: none;
  list-style: none;
}
.header a {
  text-decoration: none;
}

.section__1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.section__1 h1 {
  font-family: "Raleway", sans-serif;
  font-size: 3.5rem;
  font-weight: 400;
  color: #000000;
  margin-top: 170px;
}
.section__1 p {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 22px;
  font-weight: 400;
  color: #000000;
  text-align: center;
  width: 308px;
  margin-top: 20px;
}
.section__1 .open__your__shop {
  background-color: #08A6E4;
  color: #FFFFFF;
  font-size: 1.6rem;
  padding: 22px 92px;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  border-radius: 40px;
  text-decoration: none;
  margin-top: 28px;
  margin-bottom: 50px;
}
.section__1 .image__container {
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
.section__1 img {
  width: 200%;
  height: 200%;
}

.section__2 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.section__2 h2 {
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  line-height: 35px;
  font-weight: 400;
  color: #000000;
  text-align: center;
  width: 303px;
  margin-top: 50px;
}
.section__2 [class*=circle] {
  position: relative;
  width: 63px;
  height: 63px;
  margin-top: 25px;
  border-radius: 50%;
}
.section__2 .circle__red {
  background-color: #EB476E;
  margin-top: 25px;
}
.section__2 .circle__red::after {
  content: url("../../assets/Person.svg");
  position: absolute;
  width: 44px;
  height: 52px;
  left: 30px;
}
.section__2 .circle__blue {
  background-color: #08A6E4;
}
.section__2 .circle__blue::after {
  content: url("../../assets/Eye.svg");
  position: absolute;
  width: 44px;
  height: 52px;
  top: 13px;
  left: 20px;
}
.section__2 .circle__green {
  background-color: #55DFB4;
}
.section__2 .circle__green::after {
  content: url("../../assets/Catalog.svg");
  position: absolute;
  width: 44px;
  height: 52px;
  top: 7px;
  left: 30px;
}
.section__2 .number {
  font-family: "Raleway", sans-serif;
  font-size: 2rem;
  font-weight: 800;
  color: #000000;
  margin-top: 15px;
}
.section__2 .description {
  font-family: "Raleway", sans-serif;
  font-size: 1.7rem;
  font-weight: 400;
  color: #A5A5A5;
  margin-top: 20px;
}
.section__2 .description:last-child {
  margin-bottom: 50px;
}

.section__3 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 40px;
  background-color: #F7F7F7;
}
.section__3 .first, .section__3 .second {
  width: 315px;
  height: 370px;
  margin-bottom: 40px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FFFFFF;
}
.section__3 .third {
  width: 315px;
  height: 290px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FFFFFF;
}
.section__3 .fourth {
  width: 315px;
  height: 195px;
  position: relative;
  background-color: #55DFB4;
}
.section__3 h2 {
  position: absolute;
  font-family: "Raleway", sans-serif;
  font-size: 3rem;
  font-weight: 400;
  line-height: 35px;
}
.section__3 p {
  position: absolute;
  width: 272px;
  font-family: "Open Sans", sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 19px;
}
.section__3 .first {
  margin-top: 40px;
}
.section__3 .first img {
  width: 193px;
  height: 110px;
  position: relative;
  top: 37px;
  right: 0;
  margin-bottom: 27px;
}
.section__3 .first img:before {
  content: "";
  width: 190px;
  height: 190px;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 30px;
  background-color: #EB476E;
}
.section__3 .first h2 {
  width: 272px;
  margin-top: 174px;
  left: 22px;
}
.section__3 .first p {
  position: absolute;
  height: 72px;
  margin-top: 256px;
}
.section__3 .second img {
  width: 175px;
  height: 135px;
  top: 20px;
  left: calc(50px - 175 / 2);
  position: absolute;
}
.section__3 .second h2 {
  position: absolute;
  width: 206px;
  margin-top: 180px;
  left: 22px;
}
.section__3 .second p {
  position: absolute;
  height: 72px;
  margin-top: 268px;
}
.section__3 .third h2 {
  position: absolute;
  width: 235px;
  margin-top: 30px;
  left: 22px;
}
.section__3 .third p {
  position: absolute;
  height: 72px;
  margin-top: 188px;
}
.section__3 .fourth img {
  position: absolute;
  width: 295px;
  height: 207px;
  top: 30px;
}

.section__4 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: #FFFFFF;
}
.section__4 .pricing__box__red {
  margin-top: 40px;
  display: flex;
  width: 293px;
  height: 251px;
  border: 1px solid #EB476E;
  border-radius: 18px;
}
.section__4 .pricing__box__red .left__side {
  width: 40%;
  margin-left: 20px;
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.section__4 .pricing__box__red .left__side p:nth-child(1) {
  font-family: "Raleway", sans-serif;
  font-size: 2rem;
  font-weight: 700;
  color: #13253B;
}
.section__4 .pricing__box__red .left__side p:nth-child(2) {
  font-family: "Open Sans", sans-serif;
