.section__4 .pricing__box__red .right__side p {
  font-family: "Open Sans", sans-serif;
  font-size: 1.4rem;
  line-height: 41px;
  font-weight: 400;
  color: #000000;
}
.section__4 .begin__button__red {
  width: 150px;
  height: 50px;
  margin-top: -25px;
  background-color: #EB476E;
  color: #FFFFFF;
  font-size: 13px;
  padding: 16px 57px;
